import React from "react";
import Home from "./pages/home";
import About from "./pages/about";
import VehiclesWeapons from "./pages/vehicles_weapons";
import EnvironmentProps from "./pages/environment_props";
import Characters from "./pages/characters";
import InGameArt from "./pages/in_game_art";
import Traditional from "./pages/traditional";
import { HashRouter as Router, Route, Link } from "react-router-dom";

import "./App.css";

const Navigation = () => (
  <div className="Header-navigation">
    <Link to="/vehicles-weapons">
      <button>Vehicles/Weapons</button>
    </Link>
    <Link to="/environment-props">
      <button>Environments/Props</button>
    </Link>
    <Link to="/in-game-art">
      <button>In Game Art</button>
    </Link>
    <Link to="/traditional">
      <button>Traditional</button>
    </Link>
    <Link to="/about">
      <button>About</button>
    </Link>
  </div>
);

const Header = () => {
  return (
    <div className="Header">
      <div className="Header-Title">
        <Link to="/" className="Header-Link">
          <div className="Header-Logo">
            <span>Angela L. Mehrman</span>
            <span>3d Artist</span>
          </div>
        </Link>
      </div>
      <Navigation />
    </div>
  );
};

const App = () => {
  return (
    <Router path={process.env.PUBLIC_URL + "/"}>
      <div className="App">
        <Header />
        <div>
          <Route exact path="/" component={Home} />
          <Route exact path="/about" component={About} />
          <Route exact path="/vehicles-weapons" component={VehiclesWeapons} />
          <Route exact path="/environment-props" component={EnvironmentProps} />
          <Route exact path="/in-game-art" component={InGameArt} />
          <Route exact path="/traditional" component={Traditional} />
        </div>
      </div>
    </Router>
  );
};

export default App;
