import React, { useState, useEffect } from "react";
import Slide from "./Slide";
import { ReactComponent as ArrowIcon } from "../svgs/arrow.svg";

const modulo = (a, n) => ((a % n) + n) % n;

const autoPlay = (callback, timeout = 5000) => {
  return setTimeout(callback, timeout);
};

const Carousel = ({ slides = [], autoplay = true, timeout = 3000 }) => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [timeoutFunc, setTimeoutFunc] = useState();

  const changeSlide = (direction = 1) => {
    setCurrentIndex(modulo(currentIndex + direction, slides.length));
  };

  const incrementSlide = e => {
    e.preventDefault();
    e.stopPropagation();
    changeSlide(1);
  };

  const decrementSlide = e => {
    e.preventDefault();
    e.stopPropagation();
    changeSlide(-1);
  };

  const cleanUpTimer = () => {
    if (timeoutFunc) clearTimeout(timeoutFunc);
  };

  const setTimer = () => {
    if (timeout && autoplay) {
      cleanUpTimer();
      setTimeoutFunc(autoPlay(changeSlide, timeout));
    }
  };

  useEffect(setTimer, [currentIndex]);
  useEffect(() => cleanUpTimer);

  const { title = "", img: image } = slides[currentIndex] || {};
  const isDisabled = slides.length < 2;
  return (
    <div className="Carousel">
      <div className="Carousel-slide">
        <Slide title={title} image={image} />
      </div>
      <div className="Carousel-controls">
        <button
          disabled={isDisabled}
          className="Carousel-controls--back rotate180"
          onClick={decrementSlide}
        >
          <ArrowIcon />
        </button>
        <button
          disabled={isDisabled}
          className="Carousel-controls--forward"
          onClick={incrementSlide}
        >
          <ArrowIcon />
        </button>
      </div>
    </div>
  );
};

export default Carousel;
