import React, { useState } from "react";
import Carousel from "../components/Carousel";

const PanelOverlay = props => {
  const {
    slide: { title, img, children = [] },
    clickHandler
  } = props;

  if (children.length === 0) {
    children.push({ title, img });
  }

  return (
    <div className="PanelOverlay" onClick={clickHandler}>
      <div className="PanelOverlay-container">
        <Carousel slides={children} autoplay={false} />
      </div>
    </div>
  );
};

const Panel = ({
  img: image,
  title,
  displayTitle = false,
  orientation = "landscape",
  clickHandler
}) => {
  return (
    <div className={`Panel ${orientation}`} onClick={clickHandler}>
      <img src={image} alt={title} />
      {displayTitle && <span>{title}</span>}
    </div>
  );
};

const Gallery = ({ slides = [] }) => {
  const [selectedPanel, setSelectedPanel] = useState();

  const selectedSlide = slides.find((_slide, key) => key === selectedPanel);

  return (
    <>
      <div className="Gallery">
        {slides.map((props, key) => (
          <Panel
            key={key}
            {...props}
            clickHandler={() => setSelectedPanel(key)}
          />
        ))}
      </div>
      {selectedSlide && (
        <PanelOverlay
          slide={selectedSlide}
          clickHandler={() => setSelectedPanel(undefined)}
        />
      )}
    </>
  );
};

export default Gallery;
